.container{
    margin-top: 20%;
}

.title{
    font-weight: bold;
    font-size: 80px;
}

.intro-group{
    margin-top: 9%;
    overflow: hidden;
}

.intro{
    width: 180px;
    float: left;
    margin-right: 40px;
}

.intro-title{
    font-weight: bold;
    font-size: 40px;
}

.work-group{
    margin-top: 4%;
}

.work-single{
    position: relative;
    float: left;
}

.show-img{
    height: 180px;
    width: 300px;
    border-radius: 0.5rem;
    margin-right: 20px;
}

.work-text{
    background-color: rgba(255, 255, 255, 0.9);
    backdrop-filter: blur(16px);
    position: absolute;
    width: 180px;
    height: 95px;
    left: 130px;
    top: 95px;
    border-radius: 0.5rem;
}