.star-group{
    position: absolute;
    bottom: 0px;
    left: 0px;
    min-width: 160px;
    min-height: 154px;
    transform: scale(4) rotate(10deg);
    translate: -50% 50%;
}

.star-edge{
    position: absolute;
    height: 100px;
    width: 100px;
    background-color: rgb(243, 235, 120);
    border-radius: 0.5rem;
}