.glassy-card{
    height: 700px;
    width: 500px;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 1rem;
    backdrop-filter: blur(16px);
    transition: all 0.5s;
    position: absolute;
}

.card-icon{
    width: 90%;
    position: absolute;
    top: 50%;
    left: 50%;
    translate: -50% -50%;
}