.vertical{
    height: 200px;
    width: 50px;
    background-color: rgb(178, 231, 231);
}

.horizontal{
    height: 50px;
    width: 200px;
    background-color: rgb(178, 231, 231);
}

.left-up{
    position: absolute;
    top: 50px;
    left: 50px;
}
.left-up .horizontal,.left-up .vertical{
    position: absolute;
    top: 0;
    left: 0;
}

.right-down{
    position: absolute;
    bottom: 50px;
    right: 50px;
}

.right-down .horizontal,.right-down .vertical{
    position: absolute;
    bottom: 0;
    right: 0;
}